import { useEffect, useRef } from 'react';

export const useBlockAddedEvent = (pageId, allBlocks, onEvent) => {
    const all = allBlocks.length;
    const currentRef = useRef();
    const lastRef = useRef();
    const fnRef = useRef();

    fnRef.current = onEvent;

    if (lastRef.current) {
        lastRef.current[0] = pageId;
        lastRef.current[1] = all;
    } else {
        lastRef.current = [pageId, all];
    }

    if (!currentRef.current) {
        currentRef.current = lastRef.current;
    }

    useEffect(() => {
        const [prevId, prevAll] = currentRef.current;
        const [pageId, all] = lastRef.current;

        currentRef.current = lastRef.current;

        if (prevId === pageId && all > prevAll) {
            return fnRef.current(allBlocks[all - 1]);
        }
    }, lastRef.current);
};
