import React from 'react';
import PropTypes from 'prop-types';

import { SocialsShare } from '../SocialsShare';
import { SocialsShareBio } from '../SocialsShareBio';

export function Socials({ socials, ...rest }) {
    const Component = socials === 'bio' ? SocialsShareBio : SocialsShare;

    return <Component {...rest} />;
}

Socials.propTypes = {
    socials: PropTypes.oneOf(['bio', 'socials']),
    backPath: PropTypes.string,
};

Socials.defaultProps = {
    backPath: '..',
};
